.feed{
    flex:0.6;
    margin:15px;
}

.feed_InputContainer{
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
}



.feed_Input{
    border: 1px solid lightslategray;
    border-radius: 30px;
    display: flex;
    padding: 10px;
    color: gray;
    padding-left: 15px;
}

.feed_Input > i{
    font-size: 18px;
    font-weight: 900;
    padding-right: 4px;
    padding-top: 2px;
}

.feed_Input > form {
    display: flex;
    width: 100%;
}

.feed_Input > form > input {
    border: none;
    flex: 1;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
    outline: none;
}

.feed_InputOptions{
    display: flex;
    justify-content: space-evenly;
}
