.login{
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
    padding-bottom: 100px;
}

.login > img{
    object-fit: contain;
    height: 70px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.login > form{
    display: flex;
    flex-direction: column;

}
.login > form > input{
    width: 360px;
    height: 36px;
    font-size: 24px;
    margin-bottom: 10px;
    border-radius: 5px;
    padding: 5px;
}
.login > form > button{
    width: 360px;
    height: 36px;
    font-size: large;
    color: #fff;
    background-color: #0074b1;
    border-radius: 5px;
    padding: 5px;
    cursor: pointer;
}

.login > p{
    margin-top: 20px;
}
.login_register{
color: #0074b1;
cursor: pointer;
}