.avatar_Img {
    width: 60px;
    height: 60px;
    border-radius: 30px;
    margin-top: -5px;
  }

  .post{
      background-color: white;
      padding: 15px;
      margin-bottom: 10px;
      border-radius: 10px;
  }

  .post_header{
    display: flex;
    margin-bottom: 10px;
  }

  .post_info{
      margin-left: 10px;
  }
  .post_info > p{
      font-size: 12px;
      color: gray;
  }
  .post_body{
      overflow-wrap: anywhere;
      margin: 5px;
  }
  .post_info > h2{
      font-size: 15px;
  }
  .post_button{
      display: flex;
      justify-content: space-evenly;
  }